import axios from "axios";

export default async function getValues(method, apiURI, params) {
  const response = await axios({
    method: method,
    url: "http://localhost:5000" + apiURI,
    headers: { "Content-type": "application/json" },
    params: params,
  })
    .then((res) => {
      return res.data;
    })
    .catch((error) => {
      console.error(error);
    });
  return response;
}
