import React, { useEffect } from "react";
import { Provider } from "react-redux";
import { BrowserRouter as Router, Route, Redirect } from "react-router-dom";
import { Login, Home, Root, Header, Countries, Country } from "./components";
import store from "./_helpers/store";
import { loadUser } from "./actions/authAction";

const authToken = localStorage.getItem("token");

function App() {
  useEffect(() => {
    store.dispatch(loadUser());
  }, []);

  if (!authToken)
    return (
      <Provider store={store}>
        <Login />
      </Provider>
    );
  else {
    return (
      <Provider store={store}>
        <Router>
          <Header />
          <Redirect from="/" to="/home" />
          <Route path="/home" children={<Home />}></Route>
          <Route path="/slotmachine">
            <Root />
          </Route>
          <Route path="/countries" children={<Countries />}></Route>
          <Route path="/country/search" children={<Country />}></Route>
        </Router>
      </Provider>
    );
  }
}

export default App;
